function bestSlider() {
    var swiper = new Swiper('.best__slider.swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 30,
        observer: true,
        observeParents: true,
        observeSlideChildren: true,
    })
}

function directionSlider() {
    var swiper = new Swiper('.direction .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 30,
        navigation: {
            nextEl: '.direction .swiper-button-next',
        },
    })
}


bestSlider()
directionSlider()
