$(".place-card__tab").mouseenter(function () {
    let img = $(this).attr("data-img")
    $(this).parents(".place-card").find(".place-card__img img").attr("src",img)
    $(this).siblings(".place-card__tab").removeClass("place-card__tab--active")
    $(this).addClass("place-card__tab--active")
})

$(".place-card__tab").mouseleave(function () {
    // console.log(false)
})
